import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { DecColorService } from './../../../services/color/dec-color.service';
import { ColorPickerService } from './../../../services/color-picker/color-picker.service';
import { takeUntil, mergeMap } from 'rxjs/operators';
import { fromEvent } from 'rxjs';

@Component({
  selector: 'dec-color-picker-modal',
  template: `<div fxLayout="column">
  <div class="color-preview" [ngStyle]="{'backgroundColor': hex}">
    <dec-icon font="mat" class="close-preview" (click)="close()">close</dec-icon>
  </div>
  <div class="color-options-container" fxLayout="column">
    <div fxLayout="row" fxLayoutAlign="start center" fxLayoutGap="8px">
      <span>R</span>
      <mat-slider fxFlex min="0" max="255" step="1" [value]="red" [(ngModel)]="red" (ngModelChange)="changeRgbValue()"></mat-slider>
    </div>
    <div fxLayout="row" fxLayoutAlign="start center" fxLayoutGap="8px">
      <span>G</span>
      <mat-slider fxFlex min="0" max="255" step="1" [value]="green" [(ngModel)]="green" (ngModelChange)="changeRgbValue()"></mat-slider>
    </div>
    <div fxLayout="row" fxLayoutAlign="start center" fxLayoutGap="8px">
      <span>B</span>
      <mat-slider fxFlex min="0" max="255" step="1" [value]="blue" [(ngModel)]="blue" (ngModelChange)="changeRgbValue()"></mat-slider>
    </div>

    <div fxLayout="column" fxLayoutAlign="start" fxLayoutGap="16px" class="color-input-container">

      <div fxLayout="row" fxLayoutAlign="start center" fxLayoutGap="16px">

        <div fxFlex>RGB</div>

        <input matInput type="number" min="0" max="255" value="red" [(ngModel)]="red" (ngModelChange)="changeRgbValue()"
          autocomplete="off">

        <input matInput type="number" min="0" max="255" value="green" [(ngModel)]="green" (ngModelChange)="changeRgbValue()"
          autocomplete="off">

        <input matInput type="number" min="0" max="255" value="blue" [(ngModel)]="blue" (ngModelChange)="changeRgbValue()"
          autocomplete="off">

      </div>

      <div fxLayout="row" fxLayoutAlign="start center" fxLayoutGap="16px">

        <div fxFlex>HEX</div>

        <input matInput type="text" value="hex" [(ngModel)]="hex" autocomplete="off">

        <dec-icon font="mat" (click)="startColorPicker()">colorize</dec-icon>

      </div>

    </div>

  </div>
</div>
`,
  styles: [`.color-preview{height:320px;margin:0;padding:0;position:relative;cursor:-webkit-grab;cursor:grab}.color-preview .close-preview{position:absolute;right:0;top:0;cursor:pointer}.color-preview .close-preview:hover{color:#fff}.color-options-container{background-color:#eee;padding:0 16px;box-shadow:0 11px 15px -7px rgba(0,0,0,.2),0 24px 38px 3px rgba(0,0,0,.14),0 9px 46px 8px rgba(0,0,0,.12)}.color-options-container .color-input-container{margin-bottom:16px}.color-options-container .color-input-container input{text-align:center;border-bottom:1px solid #ccc}.color-options-container .color-input-container input:focus{border-color:rgba(0,0,0,.7)}.color-options-container .color-input-container input[type=number]::-webkit-inner-spin-button,.color-options-container .color-input-container input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none}.color-options-container .color-input-container input[type=number]{-moz-appearance:textfield}.color-options-container .color-input-container dec-icon{cursor:pointer}`]
})
export class DecColorPickerModalComponent implements OnInit {

  red: number;
  green: number;
  blue: number;

  hex: string;

  rgb: string;

  constructor(
    private dialogRef: MatDialogRef<DecColorPickerModalComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private decColorService: DecColorService,
    private colorPickerService: ColorPickerService
  ) {
    this.hex = this.data.color;
  }

  ngOnInit() {
    this.load();
    this.dragInit();
  }

  private load() {
    this.rgb = this.hex ? this.decColorService.hexToRgb(this.hex) : null;
    if (this.rgb) {
      this.red = parseInt(this.rgb[0], 10);
      this.green = parseInt(this.rgb[1], 10);
      this.blue = parseInt(this.rgb[2], 10);
      this.rgb = `rgb(${this.red},${this.green},${this.blue})`;
    }
  }

  private dragInit() {

    const dragItem: HTMLDivElement = document.querySelector('.color-picker-container');
    const dragger: HTMLDivElement = document.querySelector('.color-preview');

    const move$ = fromEvent(document, 'mousemove');
    const down$ = fromEvent(dragger, 'mousedown');
    const up$ = fromEvent(document, 'mouseup');

    const drag$ = down$.pipe(
      mergeMap(() => {
        dragItem.style.position = 'absolute';
        return move$.pipe(takeUntil(up$));
      })
    );

    drag$.subscribe((event: MouseEvent) => {
      dragItem.style.top = `${event.clientY - 160}px`;
      dragItem.style.left = `${event.clientX - 160}px`;
    });

  }

  changeRgbValue() {
    this.hex = this.decColorService.rgbToHex(this.red || 0, this.green || 0, this.blue || 0);
    this.rgb = this.decColorService.hexToRgb(this.hex, true);
  }

  startColorPicker() {
    const colorContainer = document.getElementById('colorContainer');
    colorContainer.style.display = 'none';
    this.colorPickerService.start.subscribe((color: string) => {
      colorContainer.style.display = 'block';
      this.hex = color;
      this.load();
    });
  }

  close() {
    this.dialogRef.close(this.hex);
  }

}
